@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.mobile-galery {
    display: flex;
    justify-content: space-between;
    margin: 0 -25px 32px -24px;

    @include respond-to("tablet") {
        margin-left: -42px;
        margin-right: -42px;
    }

    @include respond-to("laptop") {
        display: none;
    }
}

.image-with-badge {
    &_mobile-1 {
        width: calc(60% - 1px);
    }

    &_mobile-2 {
        width: calc(40% - 3px);
    }
}

.image-with-badge {
    position: relative;
}

.badge-text {
    position: absolute;
    top: 16px;
    left: 16px;
    padding: 4px 8px;
    border-radius: 4px;
    text-transform: uppercase;
    font-size: 13px;
    line-height: 20px;
    color: #262d33;
    background-color: get_color("gray", "0");

    &_has-accent {
        @include color("primary", "red");
    }

    @include respond-to("laptop") {
        font-size: 17px;
        line-height: 24px;
    }
}

.subtitle {
    margin-bottom: 4px;

    @include respond-to("laptop-L") {
        margin-bottom: 8px;
    }
}

.title {
    margin-bottom: 0;

    @include respond-to("tablet") {
        font-size: 44px;
        line-height: 44px;
    }

    @include respond-to("laptop-L") {
        font-size: 56px;
        line-height: 64px;
    }
}

.button {
    width: fit-content;
    margin-top: 24px;

    @include respond-to("laptop-L") {
        margin-top: 36px;
    }
}

.first-row {
    @include respond-to("laptop") {
        display: flex;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 30px;
    }
}

.desktop-galery {
    display: none;

    @include respond-to("laptop") {
        display: flex;
        position: relative;
        width: 50%;
    }
}

.image-with-badge_desktop {
    display: inline-block;
    width: 100%;
}

.wave-background {
    position: absolute;
    z-index: -1;
    width: 400px;
    height: 480px;
    bottom: -13%;
    left: -14%;
    background-image: url("/special/images/new/teens/wave.svg");
    background-position: center bottom;

    @include respond-to("laptop-L") {
        width: 480px;
        left: -19%;
    }
}

.content {
    display: flex;
    flex-direction: column;

    @include respond-to("laptop") {
        width: 50%;
        height: 450px;
        padding-left: 40px;
        justify-content: center;
    }

    @include respond-to("laptop-L") {
        height: 550px;
    }
}

.second-row {
    @include respond-to("laptop") {
        position: relative;
        display: flex;
        align-items: flex-end;
        min-height: 280px;
    }

    @include respond-to("laptop-L") {
        min-height: 343px;
    }
}

.advantages {
    display: none;

    @include respond-to("laptop") {
        position: absolute;
        top: -51px;
        left: 39%;
        display: flex;
        align-items: center;
    }

    @include respond-to("laptop-L") {
        top: -114px;
        left: 44%;
    }
}

.advantages-image {
    width: 272px;

    @include respond-to("laptop") {
        display: block;
        width: 376px;
    }
}

.advantages-list {
    @include normalizedList;

    margin-left: 40px;
    font-family: Inter, sans-serif;
}

.advantages-item {
    font-size: 17px;
    line-height: 28px;

    @include respond-to("laptop-L") {
        font-size: 22px;
        line-height: 32px;
    }

    &:not(:last-child) {
        padding-bottom: 24px;
        margin-bottom: 14px;
        border-bottom: 0.5px solid rgba(66, 78, 88, 0.1);
    }
}
