@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;
    width: 100%;

    @include respond-to("laptop") {
        flex-direction: row;
        justify-content: space-between;
    }
}

.content {
    @include respond-to("tablet") {
        min-height: 425px;
    }

    @include respond-to("laptop") {
        display: flex;
        flex-direction: column;
        justify-content: center;
        flex-grow: 0;
        width: 50%;
        min-height: 530px;
        padding-right: 20px;
    }

    @include respond-to("laptop-L") {
        min-height: 530px;
    }
}

.title_small {
    font-size: 34px;

    @include respond-to("mobile") {
        word-wrap: break-word;
    }
}

.title {
    margin-bottom: 24px;
    word-wrap: break-word;

    @include respond-to("laptop") {
        margin-bottom: 16px;
    }
}

.description {
    margin-bottom: 24px;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop-L") {
        margin-bottom: 36px;
        font-size: 22px;
        line-height: 32px;
    }
}

.mobile-galery {
    display: flex;
    justify-content: space-between;
    margin: 0 -24px 24px;

    @include respond-to("tablet") {
        margin-left: -42px;
        margin-right: -42px;
    }

    @include respond-to("laptop") {
        display: none;
    }
}

.mobile-image {
    &_1 {
        width: calc(40% - 3px);
    }

    &_2 {
        width: calc(60% - 1px);
    }
}

.desktop-galery {
    display: none;

    @include respond-to("laptop") {
        display: inherit;
        position: relative;
        width: 50%;
    }
}

.desktop-image {
    &_1 {
        z-index: 1;
        position: absolute;
        top: 0;
        right: 244px;
        width: 44%;

        @include respond-to("laptop-L") {
            right: 312px;
        }
    }

    &_2 {
        position: absolute;
        top: 40px;
        right: 0;
        width: 69%;
    }
}

.wave-background {
    position: absolute;
    z-index: -1;
    width: 59%;
    height: 384px;
    top: -83px;
    right: -13%;
    background-image: url("/special/images/new/teens/wave.svg");
    background-position: center bottom;

    @include respond-to("laptop-L") {
        top: -96px;
        right: -114px;
        width: 392px;
        height: 480px;
    }
}

.button {
    width: fit-content;
}
