@import "style/mixins.module.scss";

.root {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.title {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        margin-bottom: 44px;
    }
}

.container {
    @include respond-to("mobile") {
        margin-right: -24px;
        margin-bottom: 42px;
        margin-left: -24px;
    }
}

.content {
    display: flex;
    width: fit-content;

    @include respond-to("mobile") {
        margin-left: 10px;
    }
}

.step {
    width: 231px;
    margin-right: 8px;

    @include respond-to("tablet") {
        width: calc((100% - 80px) / 3);
        margin-right: 0;

        & + & {
            margin-left: 40px;
        }
    }
}

.first-row {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 16px;

    @include respond-to("laptop") {
        margin-bottom: 20px;
    }
}

.step-icon {
    display: none;

    @include respond-to("laptop") {
        display: inherit;

        &_1 {
            @include color("gray", "-2");
        }

        &_2 {
            @include color("gray", "-3");
        }

        &_3 {
            @include color("primary", "red");
        }
    }
}

.image {
    width: 100%;

    @include respond-to("laptop") {
        width: calc(100% - 46px);
        margin-left: 4px;
    }
}

.second-row {
    display: flex;
}

.step-index {
    display: none;

    @include respond-to("mobile") {
        @include color("primary", "red");

        display: inherit;
        width: 24px;
        margin-right: 16px;
        text-align: right;
        font-family: Inter, sans-serif;
        font-size: 22px;
        line-height: 28px;
    }
}

.step-content {
    display: flex;
    flex-direction: column;
}

.step-title {
    margin-bottom: 8px;
    font-size: 22px;
    line-height: 24px;

    @include respond-to("laptop-L") {
        font-size: 28px;
        line-height: 36px;
    }
}

.step-text {
    margin: 0;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop-L") {
        font-size: 22px;
        line-height: 32px;
    }
}
