@import "style/mixins.module.scss";

.root {
    background-color: #eef3f4;
    padding-top: 36px;
    padding-bottom: 40px;
    font-family: Inter, sans-serif;
    font-size: 17px;
    line-height: 24px;
    color: #424e58;
    position: relative;
    margin-bottom: 64px;

    @include respond-to("laptop") {
        padding-bottom: 100px;
        padding-top: 93px;
        margin-bottom: 118px;
    }

    @include respond-to("laptop-L") {
        margin-bottom: 194px;
    }
}
