@import "style/mixins.module.scss";

@import "./animate.module.scss";

.root {
    position: relative;

    @include respond-to("laptop") {
        display: flex;
    }
}

.slide-container {
    position: relative;
    overflow: hidden;
    padding-bottom: 24px;
}

.slide-track {
    display: flex;
    align-items: center;
    flex-wrap: nowrap;
    transform: translateX(-100%);
}

.slide-container-image {
    position: relative;
    overflow: hidden;

    @include respond-to("laptop") {
        width: 450px;
        height: 599px;
        margin-right: 45px;
    }
}

.slide-track-titles {
    align-items: flex-start;
}

.slide-track-images {
    display: flex;
    flex-wrap: nowrap;
    transform: translateX(0);

    @include respond-to("laptop") {
        height: 100%;
    }
}

.title {
    min-width: 100%;
    font-family: Inter, sans-serif;
    margin-bottom: 0;

    @include respond-to("laptop") {
        font-size: 44px;
        line-height: 44px;
    }

    @include respond-to("laptop-L") {
        font-size: 56px;
        line-height: 64px;
    }
}

.hidden {
    position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
}

.slide-image {
    min-width: 100%;

    @include respond-to("laptop") {
        width: 100%;
        height: 100%;
    }
}

.main-photo {
    display: block;
    min-width: 100%;
    margin-bottom: 4px;
    object-fit: cover;
    height: 312px;

    @include respond-to("laptop") {
        width: 100%;
        height: 100%;
    }
}

.main-photo_small {
    height: 200px;

    @include respond-to("laptop") {
        width: 100%;
        height: 100%;
    }
}

.wrapper-titles {
    @include respond-to("laptop") {
        width: 320px;
        position: absolute;
        z-index: 99;
        padding-top: 61px;
    }

    @include respond-to("laptop-L") {
        width: 420px;
    }
}

.wrapper-images {
    margin-left: -25px;
    margin-right: -25px;
    overflow: hidden;
    position: relative;

    @include respond-to("laptop") {
        margin-left: -43px;
        margin-right: -43px;
    }

    @include respond-to("laptop") {
        display: flex;
        justify-content: flex-end;
        overflow: unset;
        margin-left: 0;
        margin-right: 0;
        width: calc((8 / 12) * 100%);
    }
}

.wrapper-text {
    @include respond-to("laptop") {
        overflow: unset;
        width: calc((4 / 12) * 100%);
    }
}

.container-images {
    display: flex;
    width: 100%;
    flex-wrap: no-wrap;
    justify-content: space-between;
    position: absolute;
    top: 204px;
    left: 0;

    @include respond-to("laptop") {
        width: 376px;
        position: absolute;
        bottom: 40px;
        left: 0;
        z-index: 20;
        top: auto;
        bottom: 41px;
    }
}

.image {
    width: calc(50% - 2px);

    @include respond-to("laptop") {
        height: 126px;
    }

    img {
        max-width: 100%;
        width: 100%;
        height: 110px;
        object-fit: cover;
    }
}

.container-text {
    min-width: 100%;
}

.subtitle {
    padding-top: 24px;
    font-size: 28px;
    line-height: 32px;
    font-weight: 700;
    margin-bottom: 16px;
    font-family: Inter, sans-serif;
}

.tooltip {
    font-size: 13px;
    line-height: 20px;
    font-weight: 400;
    margin-bottom: 8px;
    padding-left: 33px;
    margin-bottom: 10px;
    position: relative;

    &::after {
        content: "";
        display: block;
        position: absolute;
        left: 1px;
        top: 50%;
        transform: translateY(-50%);
        border-radius: 50%;
        background-color: #057ee3;
        background-image: url("/special/images/new/teens/icon-checked.svg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: 13px 9px;
        width: 21px;
        height: 21px;
    }

    @include respond-to("laptop-L") {
        font-size: 17px;
        line-height: 28px;
    }
}

.treatment {
    font-size: 13px;
    line-height: 20px;
    margin-bottom: 22px;

    @include respond-to("laptop") {
        margin-bottom: 32px;
    }

    @include respond-to("laptop-L") {
        font-size: 17px;
        line-height: 28px;
    }
}

.description {
    margin-bottom: 24px;

    @include respond-to("laptop") {
        margin-bottom: 36px;
    }

    @include respond-to("laptop-L") {
        font-size: 22px;
        line-height: 32px;
    }
}

.button-next {
    font-family: Inter, sans-serif;
    border: none;
    padding: 0;
    background-color: transparent;
    font-size: 13px;
    line-height: 20px;
    text-transform: uppercase;
    padding-right: 74px;
    position: relative;
    font-weight: 700;
    display: block;
    outline: none;
    color: #424e58;

    &::after {
        content: "";
        display: block;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
        right: 0;
        width: 70px;
        height: 20px;
        background-image: url("/special/images/new/teens/arrow.svg");
        background-position: center;
        background-repeat: no-repeat;
        background-size: 50px auto;
    }

    @include respond-to("laptop-L") {
        font-size: 17px;
        line-height: 24px;
    }
}
