@import "style/mixins.module.scss";

.root {
    background-color: get_color("primary", "red");
    @include color("gray", "0");
}

.adolescent-treatment__content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-column-gap: 24px;
    max-width: 1256px;
    margin: 0 auto;
    padding: 46px 24px;

    @include respond-to("tablet") {
        grid-column-gap: 40px;
    }

    @include respond-to("laptop") {
        grid-template-columns: repeat(12, 1fr);
        padding: 87px 42px;
    }
}

.adolescent-treatment__title {
    display: grid;
    grid-column: span 3;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("mobile") {
        margin-bottom: 24px;
    }

    @include respond-to("laptop") {
        grid-column: span 4;
    }

    @include respond-to("laptop-L") {
        grid-column: span 3;
        font-size: 28px;
        line-height: 32px;
    }
}

.adolescent-treatment__content-item {
    grid-column: span 1;

    @include respond-to("mobile") {
        display: grid;
        align-content: flex-start;
    }

    @include respond-to("laptop") {
        grid-column: span 4;
    }

    @include respond-to("laptop-L") {
        &:last-child {
            grid-column: 9 / span 4;
        }
    }
}

.adolescent-treatment__content-icon {
    width: 48px;
    height: 48px;

    @include respond-to("mobile") {
        margin-bottom: 8px;
    }
}

.adolescent-treatment__content-text {
    margin-left: 0;
    font-size: 17px;
    line-height: 24px;

    @include respond-to("laptop") {
        margin-left: 26px;
    }

    @include respond-to("laptop-L") {
        font-size: 22px;
        line-height: 32px;
    }
}
