@keyframes slideToRight {
    from {
        transform: translateX(-100%);
    }

    to {
        transform: translateX(0);
    }
}

@keyframes slideToLeft {
    from {
        transform: translateX(0);
    }

    to {
        transform: translateX(-100%);
    }
}

@keyframes slideToLeftImages {
    from {
        transform: translateX(0);
    }

    to {
        transform: translateX(-100%);
    }
}

@keyframes slideToRightImages {
    from {
        transform: translateX(-100%);
    }

    to {
        transform: translateX(0);
    }
}

@keyframes slideToLeftTeeth {
    from {
        transform: translateX(0);
    }

    to {
        transform: translateX(-100%);
    }
}

@keyframes slideToRightTeeth {
    from {
        transform: translateX(-100%);
    }

    to {
        transform: translateX(0);
    }
}

@media (min-width: 1024px) {
    @keyframes slideToLeftTeeth {
        from {
            transform: translateX(0);
            opacity: 1;
        }

        to {
            transform: translateX(-100%);
            opacity: 0;
        }
    }

    @keyframes slideToRightTeeth {
        from {
            transform: translateX(-100%);
            opacity: 0;
        }

        to {
            transform: translateX(0);
            opacity: 1;
        }
    }
}

.slideToRight {
    animation-name: slideToRight;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
}

.slideToLeft {
    animation-name: slideToLeft;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
}

.slideToLeftImages {
    animation-name: slideToLeftImages;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
}

.slideToRightImages {
    animation-name: slideToRightImages;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
}

.slideToLeftTeeth {
    animation-name: slideToLeftTeeth;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
}

.slideToRightTeeth {
    animation-name: slideToRightTeeth;
    animation-duration: 0.8s;
    animation-fill-mode: forwards;
}
