.content {
    margin-bottom: 0;
    padding-top: 8px;

    font-size: 17px;
    line-height: 24px;
}

.icon {
    flex-shrink: 0;
}

@media (min-width: 768px) {
    .root {
        display: flex;
        flex-shrink: 0;
    }

    .content {
        margin-bottom: 0;
        padding: 0 0 0 24px;

        font-size: 22px;
        line-height: 32px;
    }
}
